import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useParams, useSearchParams } from "react-router-dom";
import axios from "axios";
import banner from "../../assets/images/hand_tel.png";
import { handleCheckUrl } from "../../utils/functions";
import { appURL, serverPath } from "../../config/key";
import Storage from "../../utils/storage";

const HeroSection = () => {
  const { t } = useTranslation();
  let [searchParams] = useSearchParams();
  let { expertise } = useParams();
  const docId = searchParams.get("docId");
  localStorage.setItem("docId", docId);
  const [consultField, setConsultField] = useState("");
  const [doctorObj, setDoctorObj] = useState("");
  const languageKey = Storage.get("lang")?.apiShortCode
    ? Storage.get("lang")?.apiShortCode
    : "heb";

  const consultingFields = async () => {
    try {
      const response = await axios.get(
        `${serverPath}/consultation/getAllConsultation`
      );
      const {
        data: { code, result },
      } = response || {};

      if (code === 200) {
        let fliterConsultField = result.find((x) => {
          const field =
            expertise === x?.heb?.trim() || expertise === x?.eng.trim();
          return field;
        });
        setConsultField(fliterConsultField);
      }
    } catch (error) {
      console.log("error", error);
    }
  };

  const doctorDataById = async (docId) => {
    try {
      const response = await axios.get(
        `${serverPath}/users/getDoctorsById/${docId}`
      );
      const {
        data: { statusCode, doctor },
      } = response || {};
      if (statusCode === 200) {
        setDoctorObj(doctor[0]);
      }
    } catch (error) {
      console.log("error", error);
    }
  };

  useEffect(() => {
    consultingFields();
    doctorDataById(docId);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <div className="hero-section">
      <div className="container">
        <div className="hero-section-grid">
          <div className="hero-banner">
            <img src={banner} alt="banner" />
          </div>
          <div className="hero-banner-text">
            <h1>{t("homeBanner.heading")}</h1>
            <div className="hero-btn">
              <span
                onClick={() => {
                  handleCheckUrl(
                    `${appURL}/meetingrequest${
                      expertise && expertise !== "null"
                        ? `?expertise=${expertise}`
                        : ""
                    }`
                  );
                }}
                className={`primary-btn ${consultField ? "green-btn" : ""}`}
              >
                {expertise && consultField
                  ? t("homeBanner.Summoning a conversation") +
                    consultField[languageKey]
                  : docId
                  ? `${t("homeBanner.to schedule an appointment with doctor")}
                  ${doctorObj?.userDetails?.first_name} 
                 ${doctorObj?.userDetails?.last_name}`
                  : t("homeBanner.button")}
              </span>
            </div>
            {expertise && consultField ? (
              <div className="hero-btn">
                <span
                  onClick={() => {
                    handleCheckUrl(`${appURL}/meetingrequest/`);
                  }}
                  className="primary-btn"
                >
                  {t(
                    "homeBanner.For a summons with an expert from another field"
                  )}
                </span>
              </div>
            ) : null}
          </div>
        </div>
      </div>
    </div>
  );
};

export default HeroSection;
