import { Button, Form } from "react-bootstrap";
import banner from "../../assets/images/new/banner.svg";
import searchIconSm from "../../assets/images/new/search-icon-sm.svg";
import search from "../../assets/images/new/search.svg";

const HeroSection = () => {
  return (
    <div className="new-hero-section">
      <div className="container">
        <div className="new-hero-grid">
          <div className="new-hero-left">
            <img src={banner} alt="banner" className="img-fluid" />
          </div>
          <div className="new-hero-right">
            <div className="hero-main-heading">
              <h1>
                בריאות <span>פרימיום</span> לכולם בכל מקום ובכל זמן.
              </h1>
              <p>
                <span>Hidoc</span> עוזרת לאנשים פשוט לדבר עם רופא, הכי מהר
                ובאופן הכי נוח שאפשר! קבע תור עם רופא מומחה מהמובילים בישראל,
                במגוון רחב של תחומים, בזמינות גבוהה ובמחיר נגיש ותהנה מייעוץ
                אונליין שמאפשר לך לקיים את השיחה מכל מקום שנוח לך.
              </p>
            </div>
            <div className="search-input">
              <Form>
                <Form.Group className="search-input-form">
                  <Form.Control
                    type="search"
                    placeholder="תחום התמחות, שם הרופא..."
                  />
                  <img src={search} alt="search" className="search-icon" />
                  <Button className="black-btn">
                    <img src={searchIconSm} alt="search" />
                  </Button>
                </Form.Group>
              </Form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HeroSection;
