import Slider from "react-slick";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import avtar1 from "../../assets/images/new/doctor1.png";
import avtar2 from "../../assets/images/new/doctor2.png";
import avtar3 from "../../assets/images/new/doctor3.png";
import { TickIcon } from "../common/icon";

const ExpertSlider = () => {
  const settings = {
    dots: true,
    cssEase: "linear",
    dotsClass: "slick-dots slick-thumb",
    infinite: true,
    speed: 500,
    autoplaySpeed: 3000,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    centerPadding: "50px",
    responsive: [
      {
        breakpoint: 1025,
        settings: {
          autoplay: true,
        },
      },
      {
        breakpoint: 769,
        settings: {
          slidesToShow: 2,
          autoplay: true,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          autoplay: true,
        },
      },
    ],
  };
  const ourExpertList = [
    {
      id: 1,
      name: "ישראל ישראלי",
      category: "רופא עור, גירודים, אקזמה",
      avtar: avtar1,
    },
    {
      id: 2,
      name: 'ד"ר מיכל דבירי',
      category: "נשים, יילוד, פוריות, גינקולוגיה",
      avtar: avtar2,
    },
    {
      id: 3,
      name: 'ד"ר אמיר ווינטרוב',
      category: "פוריות",
      avtar: avtar3,
    },
    {
      id: 4,
      name: 'ד"ר מיכל דבירי',
      category: "נשים, יילוד, פוריות, גינקולוגיה",
      avtar: avtar1,
    },
    {
      id: 5,
      name: "פרופסור יוסף רוזמן",
      category: "קרדיולוגיה",
      avtar: avtar2,
    },
  ];

  return (
    <div className="new-our-expert" id="experts-section">
      <div className="container">
        <div className="new-expert-title">
          <h2>
            {" "}
            נבחרת של <span>רופאים בכירים</span> ואיכותיים במעל 40 תחומים.
          </h2>
          <div className="new-expert-list">
            <ul>
              <li>
                {" "}
                בריאות ולידה הריון
                <TickIcon />{" "}
              </li>
              <li>
                {" "}
                בריאות ולידה הריון
                <TickIcon />{" "}
              </li>
              <li>
                {" "}
                בריאות ולידה הריון
                <TickIcon />{" "}
              </li>
              <li>
                {" "}
                בריאות ולידה הריון
                <TickIcon />{" "}
              </li>
              <li>
                {" "}
                בריאות ולידה הריון
                <TickIcon />{" "}
              </li>
              <li>
                {" "}
                בריאות ולידה הריון
                <TickIcon />{" "}
              </li>
            </ul>
          </div>
        </div>
        <div className="new-expert-slider">
          <Slider {...settings}>
            {ourExpertList.map((el) => (
              <Link to="/detail" className="new-expert" key={el?.id}>
                <div className="new-expert-profile">
                  <img src={el.avtar} alt="avtar" />
                  <div className="new-expert-content">
                    <h6>{el.name}</h6>
                    <p>
                      {el.category} <TickIcon />
                    </p>
                  </div>
                </div>
              </Link>
            ))}
          </Slider>
        </div>
        <div className="feature-cta">
          <Button className="black-btn">בוא נבחר את הרופא שלך</Button>
        </div>
      </div>
    </div>
  );
};

export default ExpertSlider;
