import { useTranslation } from "react-i18next";
import Accordion from "react-bootstrap/Accordion";

const FaqList = () => {
  const { t } = useTranslation();
  const generalFaq = [
    {
      id: 1,
      ques: t("faq.que1"),
      ans: [t("faq.ans1.1"), t("faq.ans1.2")],
    },
    {
      id: 2,
      ques: t("faq.que2"),
      ans: [t("faq.ans2.1"), t("faq.ans2.2")],
    },
    {
      id: 3,
      ques: t("faq.que3"),
      ans: [t("faq.ans3.1"), t("faq.ans3.2")],
    },
    {
      id: 4,
      ques: t("faq.que4"),
      ans: [t("faq.ans4.1"), t("faq.ans4.2"), t("faq.ans4.3")],
    },
    {
      id: 5,
      ques: t("faq.que5"),
      ans: [t("faq.ans5.1")],
    },
    {
      id: 6,
      ques: t("faq.que6"),
      ans: [t("faq.ans6.1")],
    },
    {
      id: 7,
      ques: t("faq.que7"),
      ans: [t("faq.ans7.1")],
    },
  ];
  const privacyFaq = [
    {
      id: 1,
      ques: t("faq.que8"),
      ans: [t("faq.ans8.1"), t("faq.ans8.2"), t("faq.ans8.3")],
    },
    {
      id: 2,
      ques: t("faq.que9"),
      ans: [t("faq.ans9.1"), t("faq.ans9.2")],
    },
  ];
  const paymentFaq = [
    {
      id: 1,
      ques: t("faq.que10"),
      ans: [t("faq.ans10.1")],
    },
    {
      id: 2,
      ques: t("faq.que11"),
      ans: [t("faq.ans11.1")],
    },
  ];
  const technicalFaq = [
    {
      id: 1,
      ques: t("faq.que12"),
      ans: [t("faq.ans12.1")],
    },
    {
      id: 2,
      ques: t("faq.que13"),
      ans: [t("faq.ans13.1")],
    },
    {
      id: 3,
      ques: t("faq.que14"),
      ans: [t("faq.ans14.1")],
    },
    {
      id: 4,
      ques: t("faq.que15"),
      ans: [t("faq.ans15.1")],
    },
  ];
  return (
    <div className="new-faq-list-section">
      <div className="new-faq-list">
        {/* <h6> {t("faq.generalHeading")} </h6> */}
        <Accordion defaultActiveKey="0">
          {generalFaq.map((e, i) => (
            <Accordion.Item
              eventKey={`${i}`}
              key={i}
              className="first-accordian"
            >
              <Accordion.Header>{e?.ques}</Accordion.Header>
              <Accordion.Body>
                {e.ans.length > 0 &&
                  e.ans.map((el, i) => (
                    <p dangerouslySetInnerHTML={{ __html: el }} />
                  ))}
              </Accordion.Body>
            </Accordion.Item>
          ))}
        </Accordion>
      </div>

      <div className="new-faq-list">
        {/* <h6> {t("faq.privacyHeading")} </h6> */}
        <Accordion>
          {privacyFaq.map((e, i) => (
            <Accordion.Item eventKey={`${i}`} key={i}>
              <Accordion.Header>{e?.ques}</Accordion.Header>
              <Accordion.Body>
                {e.ans.length > 0 &&
                  e.ans.map((el, i) => (
                    <p dangerouslySetInnerHTML={{ __html: el }} />
                  ))}
              </Accordion.Body>
            </Accordion.Item>
          ))}
        </Accordion>
      </div>

      <div className="new-faq-list">
        {/* <h6> {t("faq.paymentHeading")} </h6> */}
        <Accordion>
          {paymentFaq.map((e, i) => (
            <Accordion.Item eventKey={`${i}`} key={i}>
              <Accordion.Header>{e?.ques}</Accordion.Header>
              <Accordion.Body>
                {e.ans.length > 0 &&
                  e.ans.map((el, i) => (
                    <p dangerouslySetInnerHTML={{ __html: el }} />
                  ))}
              </Accordion.Body>
            </Accordion.Item>
          ))}
        </Accordion>
      </div>
      <div className="new-faq-list">
        {/* <h6> {t("faq.technicalHeading")} </h6> */}
        <Accordion>
          {technicalFaq.map((e, i) => (
            <Accordion.Item eventKey={`${i}`} key={i}>
              <Accordion.Header>{e?.ques}</Accordion.Header>
              <Accordion.Body>
                {e.ans.length > 0 &&
                  e.ans.map((el, i) => (
                    <p dangerouslySetInnerHTML={{ __html: el }} />
                  ))}
              </Accordion.Body>
            </Accordion.Item>
          ))}
        </Accordion>
      </div>
    </div>
  );
};

export default FaqList;
