import { Navbar, Nav, Dropdown, Button } from "react-bootstrap";
import { Link, NavLink } from "react-router-dom";
import { useTranslation } from "react-i18next";
import "./newHeader.css";
import { handleCheckUrl } from "../../utils/functions";
import { PrimaryLogo, WhiteLogo } from "./newLogo";

const NewHeader = ({
  scrollToExperts,
  scrollToCosulting,
  allLanguages,
  handleChangeLanguage,
  currLang,
}) => {
  const { t } = useTranslation();

  return (
    <>
      <div className="header-wrapper">
        <div className="new-header">
          <div className="container">
            <div className="new-header-grid">
              <div className="logo-grid">
                <Link to="/" className="logo">
                  {PrimaryLogo}
                </Link>
              </div>

              <div className="new-menu-section">
                <div className="new-header-menu">
                  <Navbar expand="lg">
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                      <Nav>
                        <NavLink className="nav-link mobile-view-link" to="/">
                          {WhiteLogo}
                        </NavLink>
                        <NavLink className="nav-link" to="/about">
                          <span>{t("about.heading")}</span>
                        </NavLink>
                        <NavLink className="nav-link mobile-view">
                          <span onClick={scrollToCosulting}>
                            {t("header.consulting")}
                          </span>
                        </NavLink>
                        <NavLink className="nav-link mobile-view">
                          <span onClick={scrollToExperts}>
                            {t("header.expert")}
                          </span>
                        </NavLink>
                        <NavLink className="nav-link" to="/contact">
                          {t("header.contact")}
                        </NavLink>
                      </Nav>
                    </Navbar.Collapse>
                  </Navbar>
                </div>
              </div>
              <div className="new-auth-box">
                <Button
                  onClick={() => {
                    handleCheckUrl("https://my.hidoc.co.il");
                  }}
                  className="black-btn"
                >
                  {t("header.connect")}
                </Button>

                <Dropdown className="lang-dropdown">
                  <Dropdown.Toggle variant="success" id="dropdown-basic">
                    {currLang?.shortCode}
                  </Dropdown.Toggle>
                  <Dropdown.Menu>
                    {allLanguages
                      .filter((l) => l.shortCode !== currLang?.shortCode)
                      .map((lang, index) => (
                        <Dropdown.Item
                          onClick={() => handleChangeLanguage(lang)}
                          key={lang?.id}
                          as="button"
                        >
                          <img src={lang.flag} alt="icon" /> {lang.name}
                        </Dropdown.Item>
                      ))}
                  </Dropdown.Menu>
                </Dropdown>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default NewHeader;
