import Slider from "react-slick";
import meddicineBlue from "../../assets/images/new/medicine-blue.svg";

const MedicineLogo = () => {
  const settings = {
    dots: false,
    cssEase: "linear",
    dotsClass: "slick-dots slick-thumb",
    className: "center",
    infinite: true,
    speed: 500,
    autoplaySpeed: 3000,
    autoplay: true,
    variableWidth: true,
    responsive: [
      {
        breakpoint: 1025,
        settings: {
          autoplay: true,
        },
      },
      {
        breakpoint: 769,
        settings: {
          slidesToShow: 2,
          autoplay: true,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          autoplay: true,
        },
      },
    ],
  };
  return (
    <div className="medicine-logo-section">
      <div className="medicine-setion-title">
        <h2>
          <span> Hidoc</span>
          פלטפורמה לרפואת אונליין
        </h2>
        <p>הם בחרו לעבוד איתנו בשיתוף פעולה ואנחנו ממש שמחים על כך.</p>
      </div>
      <div className="medicine-slider-section">
        <Slider {...settings}>
          <div className="medicine-slider-logo">
            <img src={meddicineBlue} alt="icon" />
          </div>

          <div className="medicine-slider-logo">
            <img src={meddicineBlue} alt="icon" />
          </div>

          <div className="medicine-slider-logo">
            <img src={meddicineBlue} alt="icon" />
          </div>
          <div className="medicine-slider-logo">
            <img src={meddicineBlue} alt="icon" />
          </div>
          <div className="medicine-slider-logo">
            <img src={meddicineBlue} alt="icon" />
          </div>
          <div className="medicine-slider-logo">
            <img src={meddicineBlue} alt="icon" />
          </div>
        </Slider>
      </div>
    </div>
  );
};

export default MedicineLogo;
