import { Button } from "react-bootstrap";
import FaqList from "./faqList";

const FaqSection = () => {
  return (
    <div className="faq-section">
      <div className="container">
        <div className="new-faq-title">
          <h2>שאלות תשובות</h2>
          <p>
            כאן זה הרגע לשאול שאלות שאולי יעזרו לכם לקבל מענה פרקטי להמשך התהליך
            איתנו
          </p>
        </div>
        <FaqList />
        <div className="still-que-grid">
          <h3>עדיין יש שאלות?</h3>
          <p>בואו נדבר יחד, תיצרו איתנו קשר ונשמח לענות על הכל</p>
          <Button className="border-btn">צור קשר</Button>
        </div>
      </div>
    </div>
  );
};

export default FaqSection;
