import { useTranslation } from "react-i18next";
import avtar1 from "../../assets/images/ourExperts/gherta.jpg";
import avtar2 from "../../assets/images/ourExperts/michal.jpg";
import avtar3 from "../../assets/images/ourExperts/amir.jpg";
import avtar4 from "../../assets/images/ourExperts/yosef.jpg";
import avtar5 from "../../assets/images/ourExperts/ayala.jpg";
import avtar6 from "../../assets/images/ourExperts/oswaldo.jpg";
import avtar7 from "../../assets/images/ourExperts/anna.jpg";
import avtar8 from "../../assets/images/ourExperts/efrat.jpeg";
import avtar9 from "../../assets/images/ourExperts/yitzhak.jpg";
import { handleCheckUrl } from "../../utils/functions";

const OurExpert = () => {
  const { t } = useTranslation();
  const ourExpertList = [
    {
      id: 1,
      name: 'ד"ר גרטה בריל',
      category: "אנדוקרינולוגיה ילדים, רפואת ילדים, אנדוקרינולוגיה",
      avtar: avtar1,
    },
    {
      id: 2,
      name: 'ד"ר מיכל דבירי',
      category: "נשים, יילוד, פוריות, גינקולוגיה",
      avtar: avtar2,
    },
    {
      id: 3,
      name: 'ד"ר אמיר ווינטרוב',
      category: "פוריות",
      avtar: avtar3,
    },
    {
      id: 4,
      name: "פרופסור יוסף רוזמן",
      category: "קרדיולוגיה",
      avtar: avtar4,
    },
    {
      id: 5,
      name: "פרופסור איילה פולק",
      category: "רפואת עיניים",
      avtar: avtar5,
    },

    {
      id: 6,
      name: 'ד"ר אוסוואלדו-מאיר רבינוביץ׳',
      category: "גינקולוגיה",
      avtar: avtar6,
    },
    { id: 7, name: 'ד"ר אנה גלוחוב', category: "סקסולוגיה", avtar: avtar7 },
    {
      id: 8,
      name: 'ד"ר אפרת סלומון-כהן',
      category: "דרמטולוגיה",
      avtar: avtar8,
    },
    {
      id: 9,
      name: "יצחק זיו-נר",
      category: "שיקום אורטופדי",
      avtar: avtar9,
    },
  ];

  return (
    <div className="our-expert" id="experts-section">
      <div className="container">
        <div className="our-expert-title">
          <h2> {t("ourExpert.heading")}</h2>
          <p>{t("ourExpert.para1")}</p>
          <p>{t("ourExpert.para2")}</p>
        </div>
        <div className="our-expert-grid">
          {ourExpertList.map((el, i) => (
            <div className="our-expert-box" key={i}>
              <div className="our-expert-profile">
                <img src={el.avtar} alt="avtar" />
              </div>
              <div className="our-expert-content">
                <h6>{el.name}</h6>
                <p>{el.category}</p>
              </div>
            </div>
          ))}
        </div>
        <div className="appointment-btn">
          <span
            onClick={() => {
              handleCheckUrl("https://my.hidoc.co.il/meetingrequest");
            }}
            className="secondary-btn"
          >
            {t("homeBanner.button")}
          </span>
        </div>
      </div>
    </div>
  );
};

export default OurExpert;
