import { useEffect, useState } from "react";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { Spinner } from "react-bootstrap";
import axios from "axios";
import PhoneInput from "react-phone-input-2";
import { useTranslation } from "react-i18next";
import { serverPath } from "../../config/key";
import InfoModal from "../common/infoModal";

const ContactSection = () => {
  const { t } = useTranslation();
  // inital state
  const contactInit = {
    name: "",
    phone: "",
    email: "",
    message: "",
  };

  // States
  const [userGeoData, setUserGeoData] = useState({});
  const [fieldState, setFieldState] = useState({ ...contactInit });
  const [loader, setLoader] = useState(false);
  const [errors, setErrors] = useState(fieldState);
  const [show, setShow] = useState(false);
  const [formStatus, setFormStatus] = useState("");
  const [isChecked, setIsChecked] = useState(false);

  let nameValue = fieldState?.name.split(" ");

  const handleIsCheck = () => {
    setIsChecked(!isChecked);
  };

  // utils function
  const handleShow = () => setShow(!show);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFieldState({ ...fieldState, [name]: value });
    setErrors({ ...errors, [name]: "" });
  };
  const handleChangePhone = (value) => {
    setFieldState({ ...fieldState, phone: value });
    setErrors({ ...errors, phone: "" });
  };

  const validateContactField = () => {
    let valid = true;
    let error = {};
    if (fieldState.name.trim() === "") {
      error.name = t("info.firstName");
      valid = false;
    }
    if (fieldState.phone.trim() === "") {
      error.phone = t("info.phone");
      valid = false;
    }
    if (fieldState.email.trim() === "") {
      error.email = t("info.email");
      valid = false;
    }
    if (!isChecked) {
      error.isCheck = "זה נדרש";
      valid = false;
    }
    return { valid, error };
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoader(true);
    setErrors({});
    const contactValidation = validateContactField();
    if (!contactValidation.valid) {
      setErrors({ ...contactValidation.error });
      setLoader(false);
      return;
    }
    try {
      const { phone, email, message } = fieldState;
      const response = await axios.post(`${serverPath}/public/contact`, {
        phone,
        email,
        message,
        firstName: nameValue[0],
        lastName: nameValue.length > 1 ? nameValue[1] : nameValue[0],
      });
      const {
        data: { code },
      } = response || {};
      setFieldState({ ...contactInit });
      if (code === 200) {
        handleShow();
        setFormStatus("success");
        setLoader(false);
      }
    } catch (error) {
      setLoader(false);
      setFormStatus("error");
      handleShow();
    }
  };

  const originData = async () => {
    const res = await axios.get("https://ipapi.co/json");
    setUserGeoData(res.data);
  };
  useEffect(() => {
    originData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  return (
    <>
      <div className="contact-section">
        <div className="container">
          <div className="new-contact-title">
            <h2>צור קשר איתנו</h2>
            <p>מבטיחים לחזור אליך במהרה</p>
          </div>
          <div className="new-contact-form">
            <Form onSubmit={handleSubmit}>
              <Form.Group className="form-group">
                <Form.Label>שם מלא</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="שם מלא"
                  name="name"
                  onChange={handleChange}
                />
                {!!errors?.name && (
                  <p className="text-danger">{errors?.name}</p>
                )}
              </Form.Group>
              <Form.Group className="form-group">
                <Form.Label>טלפון ליצירת קשר</Form.Label>
                <PhoneInput
                  country={userGeoData?.country_code?.toLowerCase() || "il"}
                  onChange={handleChangePhone}
                  inputProps={{
                    name: "phone",
                    placeholder: t("contact.phonePlaceholder"),
                  }}
                  style={{ direction: "rtl" }}
                />
                {!!errors?.phone && (
                  <p className="text-danger">{errors?.phone}</p>
                )}
              </Form.Group>

              <Form.Group className="form-group">
                <Form.Label>אימייל</Form.Label>
                <Form.Control
                  type="email"
                  placeholder={t("contact.emailPlaceholder")}
                  name="email"
                  onChange={handleChange}
                />
                {!!errors?.email && (
                  <p className="text-danger">{errors?.email}</p>
                )}
              </Form.Group>

              <Form.Group className="form-group">
                <Form.Label>{t("contact.message")}</Form.Label>
                <Form.Control
                  as="textarea"
                  rows={6}
                  placeholder={t("contact.msgPlaceholder")}
                  name="message"
                  onChange={handleChange}
                />
              </Form.Group>
              <Form.Group className="form-group" controlId="formBasicCheckbox">
                <Form.Check
                  type="checkbox"
                  label="מסכים לתנאי השימוש"
                  checked={isChecked}
                  onChange={handleIsCheck}
                />
                {!!errors?.isCheck && (
                  <p className="text-danger">{"זה נדרש"}</p>
                )}
              </Form.Group>
              <Button className="black-btn" type="submit" disabled={loader}>
                {loader ? <Spinner animation="border" /> : "שלח הודעה"}
              </Button>
            </Form>
          </div>
        </div>
      </div>
      <InfoModal show={show} handleShow={handleShow} formStatus={formStatus} />
    </>
  );
};

export default ContactSection;
