import { Button } from "react-bootstrap";
import FaqList from "../newHome/faqList";

const ConsultingDetailSection = () => {
  return (
    <div className="consulting-detail-section">
      <div className="container">
        <div className="consulting-detail-grid">
          <div className="consulting-detail-left">
            <FaqList />
          </div>
          <div className="consulting-detail-right">
            <h2>בריאות ולידה הריון בריאות האישה הריון ולידה בדיקות האישה</h2>
            <p>
              מומחה מהמובילים בישראל, במגוון רחב של תחומים, בזמינות גבוהה ובמחיר
              נגיש ותהנה מייעוץ אונליין שמאפשר לך לקיים את השיחה מכל מקום שנוח
              לך.
            </p>
            <Button className="black-btn">בוא נבחר את הרופא שלך</Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ConsultingDetailSection;
